html {
  box-sizing: border-box;
  font-size: 16px;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
}

.container {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
}

.container__row {
  flex: 100% 1 0;
}

.container__pane {
  flex: 50% 1 0;
}

.container__item {
  flex-grow: 1;
}

.padded-card {
  padding: 1rem;
}

.flex-card {
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: center;
}

.flex-card__item {
  flex-grow: 1;
}

.textarea {
  width: 100%;
  min-height: 25rem;
  resize: none;
  margin-bottom: 1rem;
}

.editor {
  display: flex;
  flex-direction: column;
}

.editor__header {
  flex: auto 0 0;
}

.editor__area {
  flex: 100% 1 1;
}

.editor__area.ace_editor {
  border: 1px solid #000;
  font-size: 1em;
  line-height: 1.5;
}

.editor__error-marker {
  position: absolute;
  background: red;
  opacity: 0.3;
}

/* this rule is really ugly, but i can't seem to find a way to override p5? */
.emoji canvas {
  width: 100% !important;
  height: auto !important;
}
